import React from 'react';
import Header from './header/Header';
import Main from './main/Main';
import LandingPage from './landingPage/LandingPage';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLandingPage: true,
    };

    this.toggleLandingPage = this.toggleLandingPage.bind(this);
  }

  toggleLandingPage() {
    const { isLandingPage } = this.state;

    this.setState({
      isLandingPage: !isLandingPage,
    });
  }

  render() {
    const { isLandingPage } = this.state;

    return (
      <div className="App">
        <Header
          toggleLandingPage={this.toggleLandingPage}
          isLandingPage={isLandingPage}
        />

        {isLandingPage && <LandingPage toggleLandingPage={this.toggleLandingPage} />}
        {!isLandingPage && <Main />}
      </div>
    );
  }
}

export default App;
