import React from 'react';
import PropTypes from 'prop-types';
import Login from './Login';
import Logout from './Logout';
import './header.scss';

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLogin: false,
      userImg: null,
      userName: null,
    };

    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
  }

  login(response) {
    const { givenName, imageUrl } = response.profileObj;
    this.setState({
      isLogin: true,
      userImg: imageUrl,
      userName: givenName,
    });
  }

  logout() {
    this.setState({
      isLogin: false,
      userImg: null,
      userName: null,
    });
  }

  render() {
    const { isLogin, userImg, userName } = this.state;
    const { toggleLandingPage } = this.props;
    return (
      <div className="app-header">

        <button
          className="piskel-button"
          onClick={toggleLandingPage}
          type="button"
        >
          <h1>Piskel</h1>
        </button>

        <div className="header-buttons-container">
          {!isLogin && <Login login={this.login} />}
          {isLogin && <Logout userName={userName} userImg={userImg} logout={this.logout} />}
        </div>
      </div>
    );
  }
}

Header.propTypes = {
  toggleLandingPage: PropTypes.func.isRequired,
};


export default Header;
