import makeFrameCopy from './makeFrameCopy';

export default function joinFrames(firstFrame, secondFrame) {
  const clone = makeFrameCopy(firstFrame);

  secondFrame.forEach((row, x) => {
    row.forEach((cell, y) => {
      if (cell) {
        clone[x][y] = cell;
      }
    });
  });

  return clone;
}
