import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircle, faSquare, faHandPaper } from '@fortawesome/free-regular-svg-icons';
import {
  faTimes,
  faPen,
  faFillDrip,
  faEraser,
  faThumbtack,
  faRadiation,
  faSun,
  faGripLinesVertical,
  faUndoAlt,
  faChessBoard,
  faEyeDropper,
  faMagic,
  faCameraRetro,
  faBroom,
} from '@fortawesome/free-solid-svg-icons';
import key from 'weak-key';
import KeyboardSettingsButton from './KeyboardSettingsButton';
import constants from '../constants';


export default class KeyboardModalMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeKeyButton: '',
    };

    this.toggleMenuStatus = this.toggleMenuStatus.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.keyboardSettingsWindow = React.createRef();
  }

  componentWillMount() {
    this.root = document.createElement('div');
    this.root.className = 'modal-keyboard-menu';
    document.body.appendChild(this.root);
  }

  componentWillUnmount() {
    document.body.removeChild(this.root);
  }


  handleClick(id, event) {
    event.stopPropagation();
    const { buttonsConfiguration, changeButtonConfig } = this.props;
    const keyboardSettingsWindow = this.keyboardSettingsWindow.current;

    keyboardSettingsWindow.onkeydown = (e) => {
      e.stopPropagation();
      const letter = e.code.slice(3, 5).toUpperCase();
      const index = constants.toolButtonsNames.indexOf(id);
      const index2 = buttonsConfiguration.indexOf(letter);
      if (index2 + 1) buttonsConfiguration[index2] = null;

      changeButtonConfig(buttonsConfiguration);

      buttonsConfiguration[index] = letter;
      this.setState({
        activeKeyButton: '',
      });

      keyboardSettingsWindow.onkeydown = null;
    };

    this.setState({
      activeKeyButton: id,
    });
  }

  toggleMenuStatus(e) {
    e.stopPropagation();

    this.setState({
      activeKeyButton: '',
    });

    this.keyboardSettingsWindow.current.classList.toggle('active');
  }

  render() {
    const { activeKeyButton } = this.state;
    const { buttonsConfiguration, toggleMenuStatus } = this.props;
    const icons = [
      faPen,
      faEraser,
      faFillDrip,
      faCircle,
      faSquare,
      faThumbtack,
      faRadiation,
      faSun,
      faGripLinesVertical,
      faUndoAlt,
      faHandPaper,
      faChessBoard,
      faEyeDropper,
      faMagic,
      faCameraRetro,
      faBroom,
    ];

    return ReactDOM.createPortal(
      <div className="keyboard-settings-window" ref={this.keyboardSettingsWindow}>
        {icons.map((icon, i) => (
          <KeyboardSettingsButton
            code={buttonsConfiguration[i]}
            handleClick={this.handleClick}
            icon={icon}
            key={key(icon)}
            id={constants.toolButtonsNames[i]}
            activeKeyButton={activeKeyButton}
          />
        ))}

        <button
          className="exit-button"
          onClick={toggleMenuStatus}
          type="button"
        >
          <FontAwesomeIcon icon={faTimes} />
        </button>
      </div>,
      this.root,
    );
  }
}

KeyboardModalMenu.propTypes = {
  changeButtonConfig: PropTypes.func.isRequired,
  buttonsConfiguration: PropTypes.arrayOf(PropTypes.string).isRequired,
  toggleMenuStatus: PropTypes.func.isRequired,
};
