import React from 'react';
import PropTypes from 'prop-types';
import OpenKebouardMenuButton from './OpenKeyboardMenuButton';
import KeyboardModalMenu from './KeyboardModalMenu';


export default class KeyboardSettings extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isModalactive: false,
    };

    this.toggleMenuStatus = this.toggleMenuStatus.bind(this);
  }

  toggleMenuStatus() {
    const { isModalactive } = this.state;

    this.setState({
      isModalactive: !isModalactive,
    });
  }

  render() {
    const { isModalactive } = this.state;
    const { buttonsConfiguration, changeButtonConfig, changeToolId } = this.props;

    return (
      <div className="keyboard-settings">
        <OpenKebouardMenuButton toggleMenuStatus={this.toggleMenuStatus} />
        {isModalactive && (
          <KeyboardModalMenu
            buttonsConfiguration={buttonsConfiguration}
            changeButtonConfig={changeButtonConfig}
            changeToolId={changeToolId}
            toggleMenuStatus={this.toggleMenuStatus}
          />
        )}
      </div>
    );
  }
}

KeyboardSettings.propTypes = {
  buttonsConfiguration: PropTypes.arrayOf(PropTypes.string).isRequired,
  changeButtonConfig: PropTypes.func.isRequired,
  changeToolId: PropTypes.func.isRequired,
};
