import React from 'react';
import PropTypes from 'prop-types';
import key from 'weak-key';
import Frame from './Frame';
import AddFrameButton from './AddFrameButton';
import './FrameControle.scss';


class FramesControl extends React.Component {
  constructor(props) {
    super(props);

    this.dropZone = React.createRef();
  }

  componentDidMount() {
    const dropZone = this.dropZone.current;

    dropZone.ondragstart = (event) => {
      this.startId = event.target.getAttribute('id');
    };

    dropZone.ondragenter = (event) => {
      if (event.target.parentElement.classList.contains('frame')) {
        event.target.parentElement.classList.add('drag-hovered');
      }
    };

    dropZone.ondragleave = (event) => {
      if (event.target.parentElement.classList.contains('frame')) {
        event.target.parentElement.classList.remove('drag-hovered');
      }
    };

    dropZone.ondragover = e => e.preventDefault();

    dropZone.ondrop = (event) => {
      const { replaceLayer } = this.props;
      const targetId = event.target.getAttribute('id');

      event.preventDefault();
      event.target.parentElement.classList.remove('drag-hovered');
      if (this.startId === targetId) return;
      replaceLayer(this.startId, targetId);
    };
  }


  render() {
    const {
      onClick,
      frames,
      currentFrame,
      copyFrame,
      deleteFrame,
      addFrame,
      pixelSize,
    } = this.props;

    return (
      <div
        className="frames-controle"
        ref={this.dropZone}
      >
        {frames.map((frame, i) => (
          <Frame
            onClick={onClick}
            frames={frames}
            key={key(frame)}
            id={i}
            active={currentFrame === i}
            copyFrame={copyFrame}
            deleteFrame={deleteFrame}
            pixelSize={pixelSize}
            dragenter={this.dragEnter}
          />
        ))
        }

        <AddFrameButton
          onClick={addFrame}
        />
      </div>
    );
  }
}

FramesControl.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.array).isRequired,
  replaceLayer: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
  currentFrame: PropTypes.number.isRequired,
  copyFrame: PropTypes.func.isRequired,
  deleteFrame: PropTypes.func.isRequired,
  addFrame: PropTypes.func.isRequired,
  pixelSize: PropTypes.number.isRequired,
};

export default FramesControl;
