import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import GIFEncoder from 'gif-encoder-2';
import UPNG from 'upng-js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import renderCanvas from '../utils/renderCanvas';
import createLink from '../utils/createLink';

export default class ModalExportMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      fps: 12,
      gifBackgroundColor: '#969696',
      fileSize: 4,
      fileName: 'piskel-clone',
    };

    this.fpsInput = React.createRef();
    this.sizeInput = React.createRef();
    this.colorInput = React.createRef();
    this.nameInput = React.createRef();

    this.changeFps = this.changeFps.bind(this);
    this.changeGifSize = this.changeGifSize.bind(this);
    this.changeGifBackgroundColor = this.changeGifBackgroundColor.bind(this);
    this.changeGifName = this.changeGifName.bind(this);
    this.exportGif = this.exportGif.bind(this);
    this.exportRSSFormat = this.exportRSSFormat.bind(this);
  }

  componentWillMount() {
    this.root = document.createElement('div');
    this.root.className = 'modal-export-menu';
    document.body.appendChild(this.root);
  }

  componentWillUnmount() {
    document.body.removeChild(this.root);
  }

  choseExportFunction(format) {
    if (format === '.gif') this.exportGif();
    if (format === '.apng') this.exportApng();
    if (format === '.rss') this.exportRSSFormat();
  }

  async exportGif() {
    const {
      gifBackgroundColor,
      fps,
      fileSize,
      fileName,
    } = this.state;
    const exportSize = fileSize * 32;
    const { layers, pixelSize } = this.props;
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const encoder = new GIFEncoder(exportSize, exportSize);
    const standartCanvasSize = 768;
    const currPixelSize = (exportSize / standartCanvasSize) * pixelSize;

    canvas.width = exportSize;
    canvas.height = exportSize;
    encoder.setDelay(1000 / fps);
    encoder.start();

    for (let i = 0; i < layers[0].length; i += 1) {
      context.fillStyle = gifBackgroundColor;
      context.fillRect(0, 0, exportSize, exportSize);

      for (let j = 0; j < layers.length; j += 1) {
        renderCanvas(canvas, layers[j][i], currPixelSize, false);
      }

      encoder.addFrame(context);
      context.clearRect(0, 0, exportSize, exportSize);
    }

    encoder.finish();

    const buffer = encoder.out.getData();
    const b64encoded = btoa(String.fromCharCode.apply(null, buffer));
    const datajpg = `data:image/jpg;base64,${b64encoded}`;
    const link = createLink(datajpg, fileName, 'gif');

    link.click();
  }

  async exportApng() {
    const canvas = document.createElement('canvas');
    const { fps, fileSize, fileName } = this.state;
    const exportSize = fileSize * 32;
    const { layers, pixelSize } = this.props;
    canvas.width = exportSize;
    canvas.height = exportSize;
    const context = canvas.getContext('2d');
    const standartCanvasSize = 768;
    const currPixelSize = (exportSize / standartCanvasSize) * pixelSize;
    const framesArr = [];

    for (let i = 0; i < layers[0].length; i += 1) {
      for (let j = 0; j < layers.length; j += 1) {
        renderCanvas(canvas, layers[j][i], currPixelSize, false);
      }

      const uintarr = context.getImageData(0, 0, exportSize, exportSize).data;

      framesArr.push(uintarr.buffer);
      context.clearRect(0, 0, exportSize, exportSize);
    }

    const delay = 1000 / fps;
    const delayMap = Array(framesArr.length).fill(delay);
    const apng = UPNG.encode(framesArr, exportSize, exportSize, 0, delayMap);
    const b64encoded = btoa(String.fromCharCode.apply(null, new Uint8Array(apng)));
    const datajpg = `data:image/jpg;base64,${b64encoded}`;
    const link = createLink(datajpg, fileName, 'apng');

    link.click();
  }

  async exportRSSFormat() {
    const { layers } = this.props;
    const { fileName } = this.state;
    const result = JSON.stringify(layers);
    const data = new Blob([result], { type: 'text/plain' });
    const textFile = window.URL.createObjectURL(data);
    const link = createLink(textFile, fileName, 'rss');
    link.click();
  }

  changeFps(newFPS) {
    this.setState({
      fps: newFPS,
    });
  }

  changeGifBackgroundColor(newBackgroundColor) {
    this.setState({
      gifBackgroundColor: newBackgroundColor,
    });
  }

  changeGifSize(newFileSize) {
    this.setState({
      fileSize: newFileSize,
    });
  }

  changeGifName(newFileName) {
    this.setState({
      fileName: newFileName,
    });
  }

  render() {
    const { changeExportMenuStatus, modalMenuFormat } = this.props;
    const {
      gifBackgroundColor,
      fps,
      fileSize,
      fileName,
    } = this.state;

    return ReactDOM.createPortal(
      <div className="export-menu">
        <span>
          Export
          {' '}
          {modalMenuFormat}
        </span>
        <div className="export-field">
          File name:
          <input
            className="nameInput"
            type="text"
            value={fileName}
            onChange={() => this.changeGifName(this.nameInput.current.value)}
            ref={this.nameInput}
          />
        </div>
        {modalMenuFormat === '.gif' && (
          <div className="export-field">
            Gif background color:
            <button
              className="color-button"
              style={{ backgroundColor: gifBackgroundColor }}
              onClick={() => this.colorInput.current.click()}
              type="button"
            >
              <input
                className="color-input"
                type="color"
                onChange={() => this.changeGifBackgroundColor(this.colorInput.current.value)}
                ref={this.colorInput}
                value={gifBackgroundColor}
              />
            </button>
          </div>
        )}

        {modalMenuFormat !== '.rss' && (
          <div className="export-field">
            FPS rate:
            {' '}
            {fps}

            <input
              type="range"
              className="fps-input"
              onChange={() => this.changeFps(this.fpsInput.current.value)}
              value={fps}
              min="0"
              max="24"
              ref={this.fpsInput}
            />
          </div>
        )}

        {modalMenuFormat !== '.rss' && (
          <div className="export-field">
            File dimensions:
            {' '}
            {fileSize * 32}

            <input
              type="range"
              className="size-input"
              onChange={() => this.changeGifSize(this.sizeInput.current.value)}
              value={fileSize}
              min="2"
              max="24"
              ref={this.sizeInput}
            />
          </div>
        )}

        <button
          onClick={() => {
            this.choseExportFunction(modalMenuFormat);
            changeExportMenuStatus(null);
          }}

          className="export-button"
          type="button"
        >
          Export
        </button>

        <button
          className="export-exit-button"
          onClick={() => changeExportMenuStatus(null)}
          type="button"
        >
          <FontAwesomeIcon icon={faTimes} />
        </button>
      </div>,
      this.root,
    );
  }
}

ModalExportMenu.propTypes = {
  changeExportMenuStatus: PropTypes.func.isRequired,
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
  modalMenuFormat: PropTypes.string.isRequired,
};
