import React from 'react';
import PropTypes from 'prop-types';
import ModalExportMenu from './ModalExportMenu';

class ExportResultSetting extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      modalMenuFormat: null,
    };

    this.changeExportMenuStatus = this.changeExportMenuStatus.bind(this);
  }


  changeExportMenuStatus(toggleModalMenuFormat) {
    const { format } = this.state;
    const newFormat = format === toggleModalMenuFormat ? null : toggleModalMenuFormat;

    this.setState({
      modalMenuFormat: newFormat,
    });
  }

  render() {
    const { modalMenuFormat } = this.state;
    const { layers, pixelSize } = this.props;

    return (
      <div className="settings-area">
        <button type="button" className="settings-button" onClick={() => this.changeExportMenuStatus('.gif')}>.gif</button>
        <button type="button" className="settings-button" onClick={() => this.changeExportMenuStatus('.apng')}>.apng</button>
        <button type="button" className="settings-button" onClick={() => this.changeExportMenuStatus('.rss')}>.rss</button>
        {modalMenuFormat && (
          <ModalExportMenu
            changeExportMenuStatus={this.changeExportMenuStatus}
            modalMenuFormat={modalMenuFormat}
            layers={layers}
            pixelSize={pixelSize}
          />
        )}
      </div>
    );
  }
}

ExportResultSetting.propTypes = {
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
};

export default ExportResultSetting;
