import React from 'react';
import PropTypes from 'prop-types';
import ToolsControl from './ToolsControl';
import ColorPicker from './ColorPicker';
import KeyboardSettings from './KeyboardSettings';
import './Tools.scss';

function Tools({
  handleColorPickerClick,
  mainColor,
  secondaryColor,
  swapColor,
  changeToolId,
  activeToolId,
  buttonsConfiguration,
  changeButtonConfig,
}) {
  return (
    <div className="tools">
      <ToolsControl
        onClick={changeToolId}
        activeToolId={activeToolId}
      />

      <ColorPicker
        onClick={handleColorPickerClick}
        mainColor={mainColor}
        secondaryColor={secondaryColor}
        swapColor={swapColor}
      />

      <KeyboardSettings
        buttonsConfiguration={buttonsConfiguration}
        changeButtonConfig={changeButtonConfig}
        changeToolId={changeToolId}
      />
    </div>
  );
}

Tools.propTypes = {
  handleColorPickerClick: PropTypes.func.isRequired,
  swapColor: PropTypes.func.isRequired,
  changeToolId: PropTypes.func.isRequired,
  changeButtonConfig: PropTypes.func.isRequired,
  mainColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  secondaryColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  activeToolId: PropTypes.string.isRequired,
  buttonsConfiguration: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Tools;
