import React from 'react';
import PropTypes from 'prop-types';

export default class ImportMenu extends React.Component {
  constructor(props) {
    super(props);

    this.input = React.createRef();
  }

  render() {
    const { importLayers } = this.props;
    return (
      <div className="settings-area">
        <button
          type="button"
          className="import-button"
          onClick={() => this.input.current.click()}
        >
          Import .rss
          <input
            type="file"
            className="import-input"
            onInput={e => importLayers(e)}
            ref={this.input}
          />
        </button>
      </div>
    );
  }
}

ImportMenu.propTypes = {
  importLayers: PropTypes.func.isRequired,
};
